<template>
    <div id="demo" class="z-10">
  <!--button v-on:click="fadeMe">
    Toggle
  </button-->
  <transition name="fade" v-on:enter="enter">
    <p v-if="show">
        <span :class="state.success" v-if="directive=='success'">{{info}}</span>
         <span :class="state.info" v-else-if="directive=='info'">{{info}}</span>
         <span :class="state.warning" v-else-if="directive=='warning'">{{info}}</span>
         <span :class="state.danger" v-else>{{info}}</span>
        
        </p>
  </transition>
</div>
</template>

<script>
export default {
    //el: '#demo',
    props: {
        info:  String,
        directive: String,
        delay: Number

    },
    data() {
        return {
        show: false,
        state: {
            success: "bg-green-400 text-white text-m font-semibold py-2 px-3 ml-5 mt-2 rounded-lg",
            warning: "bg-yellow-300 text-yellow-700 text-m font-semibold py-2 px-3 ml-5 mt-2 rounded-lg",
            danger: "bg-red-400 text-white text-m font-light py-2 px-3 ml-5 mt-2 rounded-lg",
            info: "bg-blue-200 text-blue-700 text-sm font-light py-2 px-3 ml-5 mt-2 rounded-lg"
        }
      }
    },
    mounted() {
        this.fadeMe()
    },
    methods: {

      fadeMe() {
        this.show = !this.show
    },

    enter(done) {

      var self = this;

      setTimeout(function() {
        self.show = false;
      }, this.delay); // hide the message after 3 seconds
    }

  }


    
}
</script>

<style scoped>
.fade-enter-active,
.fade-leave-active {
  transition: opacity 1s
}

.fade-enter,
.fade-leave-to
/* .fade-leave-active in <2.1.8 */

{
  opacity: 0
}
</style>